.listItem {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 600px;
  padding: 10px 10px 10px 15px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 5px;
  border-radius: 50px;
  box-shadow: inset 6px 6px 6px #cbced1, inset -6px -6px 6px white;
}

.personData {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.name {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 500;
}

.number {
  margin-right: 20px;
  margin-top: 0;
  margin-bottom: 0;
}

.deleteBtn {
  border: none;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;

  background-image: linear-gradient(
    to right,
    #1fa2ff 0%,
    #12d8fa 51%,
    #1fa2ff 100%
  );
  padding: 8px 24px;
  text-align: center;
  text-transform: lowercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: #fff;
  box-shadow: 0 0 20px #eee;
  border-radius: 50px;
  min-width: 50px;
  box-shadow: 6px 6px 6px #cbced1, -6px -6px 6px #fff;
}

.deleteBtn:hover {
  box-shadow: none;
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}
