form {
  display: flex;
  flex-direction: column;
  width: 350px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 50px;
}

.formItem {
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
}

.input,
.button {
  display: block;
  width: 100%;
  padding: 0;
  border: none;
  outline: none;
  box-sizing: border-box;
}

.input {
  outline: none;
  background: #ecf0f3;
  padding: 10px;
  padding-left: 20px;
  height: 50px;
  font-size: 14px;
  border-radius: 50px;
  box-shadow: inset 6px 6px 6px #cbced1, inset -6px -6px 6px white;
}

.button {
  background: #1fa2ff;
  border-radius: 20px;
  box-shadow: 6px 6px 6px #cbced1, -6px -6px 6px #fff;
  color: #fff;
  cursor: pointer;
  text-align: center;
  text-transform: uppercase;
  font-family: inherit;
  font-size: 18px;
  font-weight: 600;
  height: 40px;
  transition: 0.5s;
}

.button:hover {
  background-position: right center; /* change the direction of the change here */
  background: #12d8fa;
  text-decoration: none;
}
